.page__body {
  background-color: #FFFFFF;
  font-family: "Opensans", "Arial", sans-serif;
  margin: 0;
}
.page-footer {
  background-color: #F1F1F1;
}
.main-nav {
  outline: 2px solid red;
}
.main-nav__wrapper {
  outline: 2px solid brown;
}
.main-nav__list {
  list-style: none;
}
.menu {
  outline: 2px solid royalblue;
}
.menu__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.menu__item a {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;
  padding-left: 30px;
  padding-top: 24px;
  padding-bottom: 31px;
  padding-right: 160px;
  display: block;
}
.menu__item:nth-of-type(odd) a {
  background-color: #63D1BB;
}
.menu__item:nth-of-type(odd) a:hover,
.menu__item:nth-of-type(odd) a:active {
  background-color: #59D5BC;
}
.menu__item:nth-of-type(even) a {
  background-color: #56C3B2;
}
.menu__item:nth-of-type(even) a:hover,
.menu__item:nth-of-type(even) a:active {
  background-color: #46C1AE;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.promo-card p {
  font-size: 15px;
  line-height: 24px;
}
.promo-card__header {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
  margin-top: 31px;
  margin-bottom: 3.5px;
}
.promo-card__wrapper {
  margin: 0 30px;
  outline: 2px solid red;
}
.promo-card__name {
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #63D1BB;
}
.promo-card__info {
  font-size: 15px;
  line-height: 24px;
}
.promo-card__price {
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  min-width: 157px;
  text-align: center;
}
.product-card {
  outline: 2px solid blueviolet;
  margin-top: 30px;
}
.about__wrapper {
  margin: 0 30px;
  outline: 2px solid salmon;
}
.about__title {
  font-weight: bold;
  font-size: 25px;
  line-height: 30px;
}
.button {
  text-align: center;
  display: block;
  border: 2px solid #444444;
  padding: 16px;
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
  color: #444444;
  background-color: #FFFFFF;
}
.catalog__list {
  padding: 0;
  list-style: none;
  margin: 0 30px;
}
.catalog {
  outline: 2px solid tomato;
}
.features {
  margin: 0;
  padding: 0;
  list-style: none;
}
.features__item {
  font-weight: bold;
  font-size: 17px;
  line-height: 20px;
}
.page-header {
  outline: 2px solid coral;
}
.page-main__header {
  background-color: #F1F1F1;
  outline: 2px solid greenyellow;
}
.page-main__title {
  padding: 74px 30px;
  text-align: center;
  font-weight: bold;
  font-size: 27px;
  line-height: 30px;
  margin: 0;
}
.page-main__title--background {
  background-image: url("../img/hero-mobile@1x.jpg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-color: #F1F1F1;
  padding-bottom: 120px;
}
.site-list {
  padding: 0;
  margin: 0;
  outline: 2px solid lightcyan;
}
.site-list__item {
  margin-left: 76px;
  margin-top: 28.5px;
  margin-bottom: 26.5px;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
.site-list__item a {
  color: #000000;
}
.user-list {
  padding: 0;
  margin: 0;
  outline: 2px solid lightgreen;
}
.user-list__item {
  margin-left: 76px;
  margin-top: 27.5px;
  margin-bottom: 27.5px;
  font-weight: bold;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
}
.user-list__item a {
  color: inherit;
}
a {
  text-decoration: none;
}
